@font-face {
  font-family: 'VT323';
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/VT323-regular/VT323-regular.eot");
  src: url("../../fonts/VT323-regular/VT323-regular.eot?#iefix") format("embedded-opentype"), local("VT323 Regular"), local("VT323-regular"), url("../../fonts/VT323-regular/VT323-regular.woff2") format("woff2"), url("../../fonts/VT323-regular/VT323-regular.woff") format("woff"), url("../../fonts/VT323-regular/VT323-regular.ttf") format("truetype"), url("../../fonts/VT323-regular/VT323-regular.svg#VT323") format("svg"); }

* {
  box-sizing: border-box; }

html, body {
  height: 100%;
  weight: 100%;
  margin: 0;
  padding: 0;
  font-family: 'VT323', monospace;
  font-size: 16px; }

input, select, textarea, button {
  font-family: inherit;
  font-size: inherit; }

table {
  border: 1px solid #4a4a4a;
  border-collapse: collapse; }

td {
  background-color: white;
  outline: 4px solid transparent;
  border: 1px solid #4a4a4a;
  padding: 0; }
  td:hover {
    outline: 4px solid #498bff; }

a {
  color: black;
  border: 1px solid #4a4a4a;
  text-decoration: none; }
  a#download-button:focus, a#download-button:focus {
    background-color: #498bff; }

#download-button {
  background-color: #498bff;
  color: white; }
  #download-button:hover {
    cursor: pointer; }

.button {
  background-color: white;
  margin: 0;
  border: 1px solid #eeeeee;
  padding: 0.5rem;
  width: 2rem;
  height: 2rem; }
  .button:hover, .button:focus {
    border: 1px solid #818181; }

.control-panel-row {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 1rem; }

.light-gray {
  background-color: #eeeeee; }

.row-item-1-2 {
  flex: 0 1 50%;
  margin: 0 1rem; }

.row-item-only {
  flex: 0 1 100%;
  margin: 0 1rem; }

.form-label {
  display: block;
  vertical-align: middle;
  line-height: 1rem;
  font-size: 1rem;
  margin-bottom: 0.5rem; }

.input-block {
  box-sizing: border-box;
  border: 1px solid #eeeeee;
  padding: 0.5rem;
  text-align: center;
  display: block;
  width: 100%;
  min-height: 2rem; }
  .input-block:hover, .input-block:focus {
    border: 1px solid #818181; }

.svg-icon {
  width: 100%;
  height: 100%; }

#main-navigation {
  background-color: white;
  color: black;
  border-bottom: none;
  padding: 1rem;
  height: 4rem;
  width: 100%;
  align-text: center;
  vertical-align: middle;
  display: flex;
  justify-content: space-between;
  align-items: center; }

#main-section {
  height: calc(100% - 54px);
  position: relative; }

#main-heading {
  display: inline-block;
  align-self: center;
  font-size: 2rem;
  font-weight: 500; }

#main-container {
  height: 100%;
  weight: 100%;
  padding: 0;
  margin: 0;
  background-color: #eeeeee;
  display: flex;
  flex-direction: column; }

#paint-preview {
  background-color: #eeeeee;
  height: 2rem;
  width: 100%;
  text-align: center; }

#grid-toggle {
  vertical-align: middle;
  display: block;
  text-align: center; }

#grid-panel {
  height: 100%;
  position: relative; }

#grid-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #eeeeee;
  max-height: 512px;
  max-width: 512px; }

#grid-table {
  width: 100%;
  height: 100%; }

#mask-level {
  width: 100%;
  height: 0%;
  background-color: black;
  opacity: 0.5;
  position: absolute;
  z-index: 2; }

#preview-image-container {
  background-color: #eeeeee;
  width: 2rem;
  height: 2rem; }

#preview-image {
  box-sizing: border-box;
  margin: 0.5rem;
  width: 1rem;
  height: 1rem; }

#control-panel {
  background-color: white;
  color: black;
  box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden; }

#control-section {
  z-index: 5;
  position: absolute;
  height: 40%;
  width: 100%;
  transition: transform 0.5s;
  transform: translateX(-100%); }

#control-section.control-section-visible {
  transform: translateX(0%); }

#grid-section {
  position: absolute;
  height: 60%;
  width: 100%;
  transform: translateX(0%);
  bottom: 0; }

#grid-section.grid-section-visible {
  transform: translateX(0%); }

.full-height {
  height: 100%; }

.center-text {
  text-align: center; }

#mask-level.visible {
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.5;
  position: absolute;
  z-index: 2;
  display: block; }

#secondary-tools {
  display: none; }
  #secondary-tools.visible {
    display: block; }

.slider:hover {
  cursor: grab; }

.slider:active {
  cursor: grabbing; }

#full-menu-button-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

#full-menu-button {
  margin: 1rem;
  border: none;
  border-radius: 0.5rem;
  outline: none;
  background-color: white;
  transition: background-color 500ms ease-in-out; }
  #full-menu-button:hover, #full-menu-button:focus {
    cursor: pointer;
    outline: none;
    background-color: #eeeeee; }
  #full-menu-button > svg {
    fill: #ff9c9b; }

#paint-color-input {
  background-color: #eeeeee; }
  #paint-color-input:hover {
    cursor: pointer; }

#reset-grid-button {
  background-color: #ff7257;
  color: white; }
  #reset-grid-button:hover {
    cursor: pointer; }

@media only screen and (min-width: 768px) {
  #control-section {
    will-change: transform;
    z-index: 5;
    position: absolute;
    height: 100%;
    width: 25%;
    transition: transform 0.5s ease-in 0s;
    transform: translateX(-100%); }
  #control-section.control-section-visible {
    transform: translateX(0%); }
  #grid-section {
    will-change: transform;
    position: absolute;
    height: 100%;
    width: 100%;
    transition: transform 0.5s ease-in 0s, width 0.5s ease-in 0s;
    transform: translateX(0%); }
  #grid-section.grid-section-visible {
    width: 75%;
    transform: translateX(33.33%); }
  #mask-level {
    display: none; }
    #mask-level.visible {
      display: none; }
  #full-menu-button-container {
    display: none; }
  #secondary-tools {
    display: block; }
  #control-section {
    background-color: white;
    height: 100%;
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.2); }
  #control-panel {
    box-shadow: none; }
  #secondary-tools {
    display: block; } }
